<template>
    <div class="form-group m-0">
      <label :for="'input_' + the_name" class="col-form-label p-0">
        <slot name="input_txt"></slot>
      </label>
      <div class="input-wrapper">
        <input type="text" class="form-control" :id="'input_' + the_name" :name="the_name"
          :placeholder="the_placeholder"
          v-model="value" @change="valueValidate">
      </div>
    </div>
</template>

<script>
import view from '@/common/es/view'
import tool from '@/common/es/tool'
import validate from '@/common/es/validate'

export default {
  name: 'ListingTimedInoput',
  props: {
    the_name: {
      type: String
    },
    the_tip_header: {
      type: String
    },
    the_placeholder: {
      type: String
    },
    the_min_length: {
      type: Number,
      default: 9
    },
    the_max_length: {
      type: Number,
      default: 10
    }
  },
  data() {
    return {
      value: ''
    }
  },
  methods: {
    tipToast(msg) {
      tool.showToast(msg)
    },
    valueValidate() {
      let res = validate.val_empty(this.value, this.the_tip_header)
      
      if (res == true) {
        res = validate.val_length(this.value, this.the_tip_header, this.the_min_length, this.the_max_length)

        if (res == true) { this.valueSubmit() } else { tool.showToast(res) }
      }
    },
    valueSubmit() {
      this.$emit('returnValue', this.value)
    }
  }
}
</script>

<style lang="sass" scoped>
@import "@/common/sass/base.sass"

.input-wrapper input
  width: 100%
  height: 24px
  border: none !important
  padding: $distance $horizonS
  padding-right: 0px

.input-wrapper
  display: inline-block
  width: 96px

</style>


