<template>
  <div class="modal fade" tabindex="-1" role="dialog" id="pricesPdfViewModal">
    <div class="modal-dialog modal-lg modal-dialog-centered modal-dialog-scrollable">
      <div class="modal-content">
        
        <div class="modal-body">
          <div class="row">
            <div class="col-sm-12 text-center fs-12 mt-2">
              <pdf-header>
                报价单
              </pdf-header>
            </div>

            <div class="col-sm-12">
              <table class="pdf-table-outter">
                <tr>
                  <td width="65%">
                    <pdf-price-member :member="the_member" v-if="the_member!=null"></pdf-price-member>
                    <pdf-price-member :member="membery" v-else></pdf-price-member>
                  </td>
                  <td>
                    <pdf-price-msg 
                      :member="the_member" 
                      :pricesTime="the_prices_timed" 
                      :pricesNum="the_prices_numed" 
                      v-if="the_member!=null"
                    >
                    </pdf-price-msg>

                    <pdf-price-msg 
                      :member="membery" 
                      :pricesTime="prices_timed" 
                      :pricesNum="prices_numed" 

                      v-else
                    >
                    </pdf-price-msg>
                  </td>
                </tr>
              </table>
            </div>

            <div class="col-sm-12">
              <pdf-prices-freight :freights="the_prices_content" v-if="the_prices_content.length > 0"></pdf-prices-freight>
              
              <pdf-prices-freight :freights="content" v-else></pdf-prices-freight>
            </div>

          </div>
          
          <pdf-footer :member="the_member" v-if="the_member!=null"></pdf-footer>
          <pdf-footer :member="membery" v-else></pdf-footer>

        </div>

        <div class="modal-footer fs-12">
          <button class="btn btn-light btn-sm close-btn" data-dismiss="modal">关闭</button>
          <button class="btn btn-outline-info btn-sm print-btn" v-if="isPrint" @click="printPdf">导出 Pdf</button>
        </div>

      </div>
    </div>
  </div>
</template>

<script>
import conn from '@/service/conn'

import service from '@/service'
import tool from '@/common/es/tool'
import view from '@/common/es/view'
import config from '@/common/config'
import PdfFooter from '@/components/Pdf/PdfFooter.vue'

import PdfHeader from '@/components/Pdf/PdfHeader.vue'
import PdfPriceMember from '@/components/Pdf/Prices/PdfPriceMember.vue'
import PdfPriceMsg from '@/components/Pdf/Prices/PdfPriceMsg.vue'
import PdfPricesFreight from '@/components/Pdf/Prices/PdfPricesFreight.vue'
import Loading from '@/components/Shimmer/Loading.vue'

export default {
  components: { 
    PdfFooter,
    PdfHeader,
    PdfPriceMember,
    PdfPriceMsg,
    PdfPricesFreight,
    Loading
  },
  name: 'PricesPdf',
  props: {
    the_prices_content: {
      type: Array
    },
    the_member: {
      type: Object
    },
    the_prices_timed: {
      type: String
    },
    the_prices_numed: {
      type: String
    },
    isPrint: {
      type: Boolean,
      default: false
    }
  },
  mounted() {
    
  },
  data() {
    return {
      pcc_id: 0,
      membery: null,
      prices_timed: null,
      prices_numed: null,
      content: [],
      isLoading: true
    }
  },
  methods: {
    invoicePrint() {
      // tool.go()
    },
    collectData(prices) {
      this.prices_numed = prices.num
      this.prices_timed = tool.timed.ser_timed(prices.start_time, false, true)
      this.membery = prices.membery

      conn.prices_content.getOneByPrices({ price_collect: prices.id }).then(res => {
        if (res.length > 0) {
          this.pcc_id = res[0].id
          this.content = JSON.parse(res[0].content)
        }
        this.isLoading = false
      })
    },
    printPdf() {
      const _router = this.$router.resolve({ path: '/pdf_prices', query: { pcc_id: this.pcc_id } })
      window.open(_router.href, '_blank')
    }
  }
}
</script>

<style lang="sass" scoped>
@import "@/common/sass/base.sass"
@import "@/common/sass/pdf.sass"
.close-btn
  padding-left: $horizonXXL
  padding-right: $horizonXXL

.print-btn
  width: 119px
</style>


