<template>
  <div>
    <table>
      <tr>
        <td class="member-face">
          <div class="img"></div>
        </td>
        <td class="pl-3" v-if="the_member!=null">
          <div>
            {{ the_member.named }}&nbsp;
            <i class="fa fa-edit text-primary hand" v-if="is_icon" @click="changeSomething"></i>
          </div>
          <div class="fs-12">编号：{{ the_member.num }}</div>
        </td>
      </tr>
    </table>
  </div>
</template>

<script>
export default {
  name: 'ListingMemberFaceCard',
  props: {
    the_member: {
      type: Object
    },
    is_icon: {
      type: Boolean
    }
  },
  methods: {
    changeSomething() {
      this.$emit('changeSignal')
    }
  }
}
</script>
<style lang="sass" scoped>
@import "@/common/sass/base.sass"

.member-face
  width: 49px
  font-size: 38px
  padding: 0

.member-face .img
  background: #f6f6f6
  width: 49px
  height: 49px
  border-radius: 100%
  line-height: 1
  margin: 0 !important

</style>


