<template>
  <div class="h-full bg-hui content-complex-wrapper">
    <div class="content-complex pt-2 pb-1">
      
      <div class="content-header mt-2">
        <h2>
          <slot name="layout_title"></slot>
        </h2>
      </div>

      <div class="content-panel def-shadow">
        <div class="content-panel-inner">
          <div class="cpc-head">

            <slot name="layout_inner_head"></slot>

          </div>
          <div class="cpc-body cpc-border-fix">

            <slot name="layout_inner_body"></slot>

          </div>
        </div>

        <div class="content-panel-footer">

          <slot name="layout_footer"></slot>
          
        </div>
      </div>

    </div>

    <slot name="layout_extra"></slot>
  </div>
</template>

<script>
export default {
  name: 'DefLayout',
}
</script>

<style lang="sass" scoped>
@import "@/common/sass/base.sass"
@import "@/common/sass/content.sass"

</style>


